<template>
  <div>
    <h3 style="text-align: center">To-do list</h3>
    <div class="d-flex flex-row justify-space-between">
      <v-text-field v-model="todo" placeholder="Enter your work to do ..."></v-text-field>
      <v-btn class="info" @click="addToList"> Add to list!</v-btn>
    </div>
    <v-card>
      <h4>List of works to do:</h4>
      <v-list dense>
        <v-list-item-group
            color="primary"
        >
          <v-list-item
              v-for="(item, i) in items"
              :key="i"
              class="mb-2"
          >
            {{ item }}
            <v-spacer></v-spacer>
            <v-btn class="red" @click="deleteTodo(i)">X</v-btn>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "BaseTodo",
  data() {
    return {
      todo: '',
      items: ['first', 'second']
    }
  }, methods: {
    addToList() {
      if (this.todo !== '') {
        this.items.push(this.todo)
      } else {
        alert('Please enter something in tex field')
      }
      this.todo = '';
    }, deleteTodo(i) {
      this.items.splice(i,1);
    }
  }
}
</script>

